 * Replace them with your own styles or remove the file completely.
 */


$tablet-breakpoint: 1024px;
$mobile-breakpoint: 767px;

$desktop-max-width: 1080px;
$responsive-width: 90vw;
$max-height: 6rem;

$desktop-padding: 6rem;
$tablet-padding: 2rem;

$gap: 2rem;
$mobile-gap: 4rem;

$monochrome-person-color: #a7ad8bFF;
$separator-color: #a7ad8b;
$text-color: #ffffff;

$person-opacity-low: 0.25;
$person-opacity-high: 1;

$drop-shadow-weak: rgba(167, 173, 139, 0.05);
$drop-shadow-strong: rgba(167, 173, 139, 0.10);
$drop-shadow-hover: rgba(167, 173, 139, 0.05);

$hover-transition-duration: 0.5s;
$hover-transition-timing: ease-in-out;

$filter-saturate: saturate(2);


.wp-block-create-block-logo-people {
	max-width: $desktop-max-width;
	width: $responsive-width;
	margin-left: auto;
	margin-right: auto;

	.people {
		position: relative;

		width: auto;

		margin-left: $desktop-padding;
		margin-right: $desktop-padding;

		display: flex;
		flex-direction: row;
		flex-wrap: none;
		justify-content: space-between;
		align-items: baseline;
		gap: $gap;

		overflow: hidden;

		img {
			height: 100%;
			max-height: $max-height;
			min-width: 0;

			background-color: transparent;
		}

		img.no_shadow.monochrome {
			transform: translateY(-1000px);
			filter: drop-shadow(0px 1000px 0 $monochrome-person-color) drop-shadow(15px 15px 0 $drop-shadow-weak);
		}

		img.no_shadow.color {
			transform: none;
			filter: drop-shadow(15px 15px 0 $drop-shadow-weak);
		}

		img.shadow.monochrome {
			transform: translateY(-1000px);
			filter: drop-shadow(0px 1000px 0 $monochrome-person-color) drop-shadow(15px 15px 0 $drop-shadow-strong);
		}

		img.shadow.color {
			transform: none;
			filter: drop-shadow(15px 15px 0 $drop-shadow-strong);
		}


		@media (max-width: $tablet-breakpoint) {
			max-width: 100%;

			margin-left: $tablet-padding;
			margin-right: $tablet-padding;
		}

		@media (max-width: $mobile-breakpoint) {
			display: none;
		}

		&:hover {
			.peopleOverlay {
				filter: none;
				mix-blend-mode: normal;
				opacity: $person-opacity-high;
			}

			img {
				filter: drop-shadow(15px 15px 0 $drop-shadow-hover) !important;
			}
		}

	}

	.peopleOverlay {
		position: absolute;

		top: 0;
		left: 0;
		bottom: 0;
		right: 0;

		margin-left: 0;
		margin-right: 0;

		z-index: 1;

		background-color: transparent;
		mix-blend-mode: luminosity;
		opacity: $person-opacity-low;
		transition: opacity $hover-transition-duration $hover-transition-timing;
		filter: $filter-saturate;

		img {
			filter: none !important;
			transform: none !important;
		}

		img.color {
			opacity: 0;
		}

		@media (max-width: $tablet-breakpoint) {
			max-width: 100%;
		}

		@media (max-width: $mobile-breakpoint) {
			display: none;
		}
	}



	.mobilePeople {
		display: none;
		gap: $mobile-gap;

		@media (max-width: $mobile-breakpoint) {
			display: flex;
			justify-content: center;
		}

		.peopleOverlay {
			gap: $mobile-gap;

			@media (max-width: $mobile-breakpoint) {
				display: flex;
			}
		}
	}

	.spacer {
		height: 24px;
		background-color: $separator-color;

		padding-left: 6px;
		padding-right: 6px;

		display: flex;
		justify-content: right;
		align-items: center;

		font-family: "Pixelify Sans", serif;
		font-optical-sizing: auto;
		font-weight: 100;
		font-style: normal;
		color: $text-color;

		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;

		.section {
			display: flex;
			justify-content: space-between;
			align-items: center;
			gap: 26px;
		}

		.radioButton {
			position: relative;
			cursor: pointer;
		}

		svg {
			height: 14px;
			width: 16px;
			transform: scale(1.6);

			path {
				fill: $text-color;
			}

			&.play.radioPart {
				cursor: pointer;
				display: none;
				margin-left: 6px;
				margin-right: 6px;
				transform: translate(0, 1px) scaleX(2.2) scaleY(1.5);
			}

			&.pause.radioPart {
				cursor: pointer;
				margin-left: 6px;
				margin-right: 6px;
				transform: translate(0, 1px) scaleX(1.6) scaleY(1.5);
			}

			&.skip.radioPart {
				cursor: pointer;
				transform: translate(0, 1px) scaleX(1.6) scaleY(1.5);
			}

			&.prev.radioPart {
				cursor: pointer;
				transform: translate(0, 1px) scaleX(1.6) scaleY(1.5);
			}

			&.radio {
				transform: translate(0, 1px) scaleX(1.6) scaleY(1.5);
				margin-left: 4px;
			}

			&.headphones {
				transform: translate(0, 1px) scale(1.6);
				z-index: 2;
			}

		}

		.radioPart {
			display: none;
		}

		.time {
			justify-self: right;
			margin-right: 8px;
		}

		.title {
			margin-left: -16px;
		}

		.album {
			margin-left: -16px;
			opacity: 0.6;
		}

		&.active {
			justify-content: space-between;

			.radioPart {
				display: inline;
			}

			.section {
				display: flex;
			}



			.radioButton {
				path {
					fill: $separator-color;
				}

				.headphonesBg {
					position: absolute;
					top: 0;
					left: 0;
					right: 0;
					bottom: 0;

					transform: scale(1.4, 0.62);

					background-color: $text-color;
				}
			}


		}

		&.pause {
			.pause {
				display: none;
			}

			.play {
				display: inline !important;
			}
		}
	}

	a {
		position: absolute;

		top: 0;
		left: 0;
		right: 0;
		bottom: 0;

		z-index: 2;
	}
}